import React from "react";
import {NavLink} from "react-router-dom";
import userIcon from "../../assets/images/UserIcon.png";
import style from "./Users.module.css";

function User({user, followingInProgress, unfollow, follow}) {
    return (
        <div>
        <span>
                    <div>
                        <NavLink to={`/profile/${user.id}`}>
                        <img src={user.photos.small != null ? user.photos.small : userIcon} className={style.userPhoto}
                             alt='avatar'/>
                        </NavLink>
                    </div>
                    <div>
                        {user.followed ?
                            <button disabled={followingInProgress.some(id => id === user.id)}
                                    onClick={() => {
                                        unfollow(user.id);
                                    }}>Unfollow</button>


                            : <button disabled={followingInProgress.some(id => id === user.id)}
                                      onClick={() => {
                                          follow(user.id)
                                      }}>Follow</button>}


                                </div>
                                </span>
            <span>
                                <span>
                                <div>{user.name}</div>
                                <div>{user.status}</div>
                                    <br/>
                                </span>
                                <span>
                                </span>
                                </span>
        </div>
    )
}

export default User